.responsive-block-editor-addons-block-blockquote-item {
  position: relative;
  padding: 60px 70px 0 70px;
  width: 100%;
  .responsive-block-editor-addons-block-blockquote-quote {
    position: absolute;
    fill: rgba(129, 141, 165, 0.32);
    width: 70px;
    height: 70px;
    transform: translateX(-50%) translateY(-30%);
    top: 20px;
    left: 30px;
    z-index: 5;

    svg {
      width: 100%;
      height: 100%;
    }
  }
  .responsive-block-editor-addons-block-blockquote-text {
    position: relative;
    z-index: 6;
  }
  .responsive-block-editor-addons-section__video-wrap {
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    position: absolute;
    overflow: hidden;
    z-index: 0;
    -webkit-transition: opacity 1s;
    -o-transition: opacity 1s;
    transition: opacity 1s;

    video {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
      width: 100%;
      height: 100%;
      z-index: 1;
      -o-object-fit: cover;
      object-fit: cover;
    }
  }
}
